.common-section-title {
  position: relative;
  margin: 0;
  padding: 0 0 0 16px;
  line-height: 1;
  font-size: 16px;
  font-weight: normal;
  color: #666666;
}
.common-section-title:before {
    content: '';
    display: inline-block;
    position: absolute;
    top: -2px;
    left: 0;
    width: 4px;
    height: 20px;
    background: #3c96ff;
}
.common-form-item {
  margin-bottom: 20px;
}
.common-form-item::after {
    display: block;
    content: "";
    clear: both;
}
.common-form-item:last-child {
    margin-bottom: 0;
}
.common-form-item > label {
    position: relative;
    float: left;
    margin-top: 6px;
    width: 100px;
    font-size: 14px;
    font-weight: bold;
    color: #666666;
    text-align: right;
}
.common-form-item > label.required:after {
      content: '*';
      position: absolute;
      top: 0px;
      right: -10px;
      color: #ff2602;
      font-family: "SimSun";
}
.common-form-item .common-form-content {
    margin-left: 120px;
    min-height: 36px;
}
.step-section-title {
  text-align: left;
  margin: 0;
  line-height: 67px;
  color: #313238;
}
.step-section-title span {
    padding-left: 30px;
    font-size: 14px;
    font-family: PingFangSC-Semibold;
    font-weight: 600;
}
.step-section-title:before {
    content: '';
    display: inline-block;
    position: relative;
    top: 4px;
    left: 20px;
    width: 2px;
    height: 20px;
    background: #A3C5FD;
}
.common-dialog .bk-dialog-header {
  padding: 20px 30px;
  border-bottom: 1px solid #ebebeb;
}
.common-dialog .bk-dialog-header .bk-dialog-title {
    text-align: left;
    line-height: 1;
}
.common-dialog .bk-dialog-tool {
  min-height: 0px;
}
.common-dialog .bk-dialog-tool .bk-dialog-close {
    margin-top: 16px;
    margin-right: 20px;
}
.common-error-tip {
  display: inline-block;
  font-size: 14px;
  line-height: 1;
  color: #ff5757;
}
.bk-dialog-wrapper .bk-dialog-footer .bk-dialog-outer button {
  width: 90px;
  height: 32px;
  line-height: 30px;
}
.common-warning-tip {
  display: inline-block;
  font-size: 14px;
  color: #ffb400;
}
.v-tooltips-container.offset-left-tooltip {
  margin-left: -7px;
}
.bk-selector label {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.bk-selector-wrapper .bk-selector-icon {
  top: 10px;
}
.bk-selector-wrapper .bk-selector-icon.clear-icon {
  top: 7px;
}
.bk-selector-wrapper .bk-selector-input {
  height: 32px;
  line-height: 32px;
}
.panagation .bk-page .page-item {
  float: none;
}
.panagation .bk-page .page-item.cur-page {
  background: #3c96ff;
}
.panagation .bk-page .page-item .page-button {
  padding: 0 7px;
}
.panagation .bk-page .page-item.disabled .page-button {
  padding: 0 5px;
  color: #737987;
}
.panagation .bk-page .page-item.disabled .page-button:hover {
    color: #737987;
}
body {
  color: #333333;
  font-family: 'Microsoft YaHei','PingFang SC','Hiragino Sans GB','SimSun','sans-serif';
}
.clearfix::after {
  display: block;
  content: "";
  clear: both;
}
html, body {
  height: 100%;
}
#app {
  height: 100%;
}
